.Wrapper {
    position: relative;
    height: 100vh;
    z-index: 0;
    overflow: hidden;
    background-color: #fafafa;
}

.back {
    position: absolute;
    width: 100vw;
    height: 50vw;
    z-index: -1;
    background-color: var(--golden-color);
    clip-path: ellipse(58vw 50vh at 50% 13%);
}

.ul {
    height: 100%;
    display: flex;
    margin-top: 1.3rem;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    align-items: center;
}

.li {
    width: 19.3rem;
    height: 80vh;
}

@media screen and (max-width: 600px) {
    .Wrapper {
        height: unset;
        min-height: 100vh;
    }
    .li {
        width: 80vw;
        margin: 3% 0;
    }
    .li:nth-child(2) {
        width: 85vw;
    }
}